/*  Home Page Photos */

.img-grid {
    margin: 0px auto;
    display: grid;
    /*  displayed in a grid  */
    grid-template-columns: .97fr;
    /*  3 Coloumns each one with equal width of 1 fraction of the grid  */
    grid-gap: 20px;
    /*  the grid gap of each row is 40 pixels  */
}

.img-wrap {
    /*  Individual items that surround the image  */
    overflow: hidden;
    /*  The image doesnt apear beyond that (scrolling...)  */
    height: 0;
    /*  height is 0  */
    padding: 15% 15%;
    /*  The padding is 50% up and down and 0 left and right  */
    /*  Padding controls height, will always be perfectly square 
                            regardless of width of the page if there is no height*/
    position: relative;
    /*  becauase the postion inside (.img-wrap img) is absolute  */
}

.img-wrap img {
    min-width: 50%;
    /*  Taking the full width of the square avalible  */
    min-height: 100%;
    /*  Taking the full width of the square avalible  */
    max-width: 50%;
    /*    */
    position: absolute;
    top: 0;
    /*  Starting at the top  */
    left: 0;
    /*  -> Left  */
}

