.img-background {
    background-color: rgb(235, 235, 235);
    box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.3);
    height: 50px;
    width: 380px;
    border-radius: 5px;
}

.img-upload-preview {
    max-width: 100%;
    border: 1px solid lightgray;
    border-radius: 5px;
    box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.3);
}

/*  imagePop  */

.backdrop {
    /*  the thing that surrounds the image fading the background */
    position: fixed;
    /*  fixed position in the top left  */
    top: 0;
    left: 0;
    width: 100%;
    /*  width and height span the entire window  */
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    /*  background and semi transparent  */
}

.backdrop img {
    /*  The image inside the backdrop  */
    display: block;
    /*    */
    max-width: 60%;
    /*  60% the width of the screen  */
    max-height: 80%;
    /*  80% the width of the screen  */
    margin: 100px auto;
    /*  100px top and bottom, auto left and right (positioning the image in the middle of the screen */
    box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
    /*  To add depth    */
    border: 3px solid white;
}

/*  User Photos  */

.user-img-grid {
    /*  Surrounds all the images  */
    margin: 20px auto;
    display: grid;
    /*  displayed in a grid  */
    grid-template-columns: 1fr 1fr 1fr;
    /*  3 Coloumns each one with equal width of 1 fraction of the grid  */
    grid-gap: 40px;
    /*  the grid gap of each row is 40 pixels  */
}

.user-img-wrap {
    overflow: hidden;
    height: 0;
    padding: 50% 0;
    /* Padding controls height, will always be perfectly square regardless of width */
    position: relative;
    opacity: 0.8;
}

.user-img-wrap img {
    min-width: 100%;
    min-height: 100%;
    max-width: 150%;
    position: absolute;
    top: 0;
    left: 0;
}

.profile-container {
    display: inline-block;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
    height: 200px;
    width: 200px;
    border-radius: 100%;
    border: 1px solid black;
    text-align: center;
    position: relative;
    overflow: hidden;
}

.profile-image {
